import React from 'react'
import './form.scss';

interface Iprops {
    children: React.ReactNode;
    onSubmit: Function;
}

function Form(props: Iprops) {
    return (
        <form onSubmit={(e: React.FormEvent) => props.onSubmit(e)}>
            {props.children}
        </form>
    )
}

export default Form
