import React from 'react'
import './button.scss';

interface Ibutton {
    children: React.ReactNode,
    fluid?: boolean,
    onClick?: React.MouseEventHandler<HTMLButtonElement>
}

const Button = (props: Ibutton) => {
    return (
        <button onClick={props.onClick} className={"custom-button " + (props.fluid ? "fluid" : "")}>{props.children}</button>
    )
}

export default Button
