import React from "react";
import MainUI from "../../components/mainUI";
import Icon from "../../media/images/favicon/favicon-16x16.png";
import { Helmet } from "react-helmet";

function Main() {
    const getAge = () => {
        return Math.floor(
            (new Date(2021, 0o3, 13).getTime() -
                new Date(2001, 9, 13).getTime()) /
                (1000 * 60 * 60 * 24) /
                31 /
                12
        );
    };

    return (
        <>
            <Helmet>
                <meta charSet="utf-8" />
                <title>Портфолио сайт | Рыбаков Андрей</title>
            </Helmet>
            <MainUI getAge={getAge} />
        </>
    );
}

export default Main;
